/**
 * SEO component that queries for data with
 *  Gatsby's useStaticQuery React hook
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React from "react";
import Head from "next/head";

import { withConfig } from "../../api/config";
import GoogleAnalytics from "./googleAnalytics";

function MyHead(props) {
  const { description, lang, meta, keywords, title, author, siteTitle, defaultTitle } = props;
  const realTitle = title ? `${siteTitle} - ${title}` : defaultTitle;
  const metas = [
    {
      name: `description`,
      content: description,
    },
    {
      property: `og:title`,
      content: title,
    },
    {
      property: `og:description`,
      content: description,
    },
    {
      property: `og:type`,
      content: `website`,
    },
    {
      name: `twitter:card`,
      content: `summary`,
    },
    {
      name: `twitter:creator`,
      content: author,
    },
    {
      name: `twitter:title`,
      content: title,
    },
    {
      name: `twitter:description`,
      content: description,
    },
  ]
    .concat(
      keywords.length > 0
        ? {
            name: `keywords`,
            content: keywords.join(`, `),
          }
        : []
    )
    .concat(meta);
  return (
    <React.Fragment>
      <Head>
        <title>{realTitle}</title>
        <meta charSet='utf-8' />
        {metas.map((m, i) => (
          <meta key={i} {...m}/>
        ))}
        <script src="https://code.jquery.com/jquery-3.3.1.slim.min.js" integrity="sha384-q8i/X+965DzO0rT7abK41JStQIAqVgRVzpbzo5smXKp4YfRvH+8abtTE1Pi6jizo" crossOrigin="anonymous"></script>
    <script src="https://cdnjs.cloudflare.com/ajax/libs/popper.js/1.14.7/umd/popper.min.js" integrity="sha384-UO2eT0CpHqdSJQ6hJty5KVphtPhzWj9WO1clHTMGa3JDZwrnQq4sF86dIHNDz0W1" crossOrigin="anonymous"></script>
    <script src="https://stackpath.bootstrapcdn.com/bootstrap/4.3.1/js/bootstrap.min.js" integrity="sha384-JjSmVgyd0p3pXB1rRibZUAYoIIy6OrQ6VrjIEaFf/nJGzIxFDsf4x0xIM+B07jRM" crossOrigin="anonymous"></script>
    {/* <script src="https://code.jquery.com/jquery-3.3.1.slim.min.js" integrity="sha384-q8i/X+965DzO0rT7abK41JStQIAqVgRVzpbzo5smXKp4YfRvH+8abtTE1Pi6jizo" crossOrigin="anonymous"></script> */}
      <link href="https://fonts.googleapis.com/css?family=Lato:300,300i,400,400i,700,700i|Open+Sans" rel="stylesheet" />
      <style type="text/css">
      {`
        body {
          font-family: 'Lato', 'Open Sans', 'Noto Sans SC', "Microsoft YaHei", sans-serif;
        }
      `}
      </style>

    </Head>
      <GoogleAnalytics />
    </React.Fragment>
    
  );
}

MyHead.defaultProps = {
  lang: `en`,
  meta: [],
  keywords: [],
  description: ``,
};

export default withConfig((props) => {
  const siteTitle = props.config.title;
  const defaultTitle = props.config.metaTitle;
  const author = props.config.author;
  const description = props.config.description;
  return <MyHead siteTitle={siteTitle} defaultTitle={defaultTitle} author={author} description={description} {...props} />
});
